@import "../../App.css";

.Info {
    padding: 72px 0px;
    margin: 0px 72px;
    display: flex;
    gap: 180px;
    align-items: center;
}
.Info .leftSection{
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.rightSection img{
    height: 60vh;
}



@media screen and (max-width: 900px) {

    .Info {
        padding: 72px 0px;
        margin: 0px 20px;
        display: flex;
        gap: 24px;
        align-items: center;
        flex-direction: column;
    }
    .Info .leftSection{
        display: flex;
        flex-direction: column;
        gap: 8px;
        order: 1;
    }
    .rightSection img{
        height: 45vh;
    }
      }
